\n * Slide animations\n */\n\n[data-aos^='slide'][data-aos^='slide'] {\n  transition-property: transform;\n\n  &.aos-animate {\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n[data-aos='slide-up'] {\n  transform: translate3d(0, 100%, 0);\n}\n\n[data-aos='slide-down'] {\n  transform: translate3d(0, -100%, 0);\n}\n\n[data-aos='slide-right'] {\n  transform: translate3d(-100%, 0, 0);\n}\n\n[data-aos='slide-left'] {\n  transform: translate3d(100%, 0, 0);\n}\n\n\n\n\n/**\n * Flip animations:\n * flip-left, flip-right, flip-up, flip-down\n */\n\n[data-aos^='flip'][data-aos^='flip'] {\n  backface-visibility: hidden;\n  transition-property: transform;\n}\n\n[data-aos='flip-left'] {\n  transform: perspective(2500px) rotateY(-100deg);\n  &.aos-animate {transform: perspective(2500px) rotateY(0);}\n}\n\n[data-aos='flip-right'] {\n  transform: perspective(2500px) rotateY(100deg);\n  &.aos-animate {transform: perspective(2500px) rotateY(0);}\n}\n\n[data-aos='flip-up'] {\n  transform: perspective(2500px) rotateX(-100deg);\n  &.aos-animate {transform: perspective(2500px) rotateX(0);}\n}\n\n[data-aos='flip-down'] {\n  transform: perspective(2500px) rotateX(100deg);\n  &.aos-animate {transform: perspective(2500px) rotateX(0);}\n}\n","// --------------------------------------------------------------\n// Animations\n// --------------------------------------------------------------\n\n@keyframes plyr-progress {\n  to {\n    background-position: $plyr-progress-loading-size 0;\n  }\n}\n\n@keyframes plyr-popup {\n  0% {\n    opacity: 0.5;\n    transform: translateY(10px);\n  }\n\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes plyr-fade-in {\n  from {\n    opacity: 0;\n  }\n\n  to {\n    opacity: 1;\n  }\n}\n","// --------------------------------------------------------------\n// Base styling\n// --------------------------------------------------------------\n\n// Base\n.plyr {\n  @include plyr-font-smoothing($plyr-font-smoothing);\n  align-items: center;\n  direction: ltr;\n  display: flex;\n  flex-direction: column;\n  font-family: $plyr-font-family;\n  font-variant-numeric: tabular-nums; // Force monosace-esque number widths\n  font-weight: $plyr-font-weight-regular;\n  line-height: $plyr-line-height;\n  max-width: 100%;\n  min-width: 200px;\n  position: relative;\n  text-shadow: none;\n  transition: box-shadow 0.3s ease;\n  z-index: 0; // Force any border radius\n\n  // Media elements\n  video,\n  audio,\n  iframe {\n    display: block;\n    height: 100%;\n    width: 100%;\n  }\n\n  button {\n    font: inherit;\n    line-height: inherit;\n    width: auto;\n  }\n\n  // Ignore focus\n  &:focus {\n    outline: 0;\n  }\n}\n\n// border-box everything\n// http://paulirish.com/2012/box-sizing-border-box-ftw/\n@if $plyr-border-box {\n  .plyr--full-ui {\n    box-sizing: border-box;\n\n    *,\n    *::after,\n    *::before {\n      box-sizing: inherit;\n    }\n  }\n}\n\n// Fix 300ms delay\n@if $plyr-touch-action {\n  .plyr--full-ui {\n    a,\n    button,\n    input,\n    label {\n      touch-action: manipulation;\n    }\n  }\n}\n","// ==========================================================================\n// Mixins\n// ==========================================================================\n\n// Nicer focus styles\n// ---------------------------------------\n@mixin plyr-tab-focus($color: $plyr-tab-focus-color) {\n  outline-color: $color;\n  outline-offset: 2px;\n  outline-style: dotted;\n  outline-width: 3px;\n}\n\n// Font smoothing\n// ---------------------------------------\n@mixin plyr-font-smoothing($mode: true) {\n  @if $mode {\n    -moz-osx-font-smoothing: grayscale;\n    -webkit-font-smoothing: antialiased;\n  }\n}\n\n// <input type=\"range\"> styling\n// ---------------------------------------\n@mixin plyr-range-track() {\n  background: transparent;\n  border: 0;\n  border-radius: calc(#{$plyr-range-track-height} / 2);\n  height: $plyr-range-track-height;\n  transition: box-shadow 0.3s ease;\n  user-select: none;\n}\n\n@mixin plyr-range-thumb() {\n  background: $plyr-range-thumb-background;\n  border: 0;\n  border-radius: 100%;\n  box-shadow: $plyr-range-thumb-shadow;\n  height: $plyr-range-thumb-height;\n  position: relative;\n  transition: all 0.2s ease;\n  width: $plyr-range-thumb-height;\n}\n\n@mixin plyr-range-thumb-active($color) {\n  box-shadow: $plyr-range-thumb-shadow, 0 0 0 $plyr-range-thumb-active-shadow-width $color;\n}\n\n// Fullscreen styles\n// ---------------------------------------\n@mixin plyr-fullscreen-active() {\n  background: #000;\n  border-radius: 0 !important;\n  height: 100%;\n  margin: 0;\n  width: 100%;\n\n  video {\n    height: 100%;\n  }\n\n  .plyr__video-wrapper {\n    height: 100%;\n    position: static;\n  }\n\n  // Vimeo requires some different styling\n  &.plyr--vimeo .plyr__video-wrapper {\n    height: 0;\n    position: relative;\n  }\n\n  // Display correct icon\n  .plyr__control .icon--exit-fullscreen {\n    display: block;\n\n    + svg {\n      display: none;\n    }\n  }\n\n  // Hide cursor in fullscreen when controls hidden\n  &.plyr--hide-controls {\n    cursor: none;\n  }\n\n  // Large captions in full screen on larger screens\n  @media (min-width: $plyr-bp-lg) {\n    .plyr__captions {\n      font-size: $plyr-font-size-captions-large;\n    }\n  }\n}\n","// --------------------------------------------------------------\n// Badges\n// --------------------------------------------------------------\n\n.plyr__badge {\n  background: $plyr-badge-background;\n  border-radius: $plyr-badge-border-radius;\n  color: $plyr-badge-text-color;\n  font-size: $plyr-font-size-badge;\n  line-height: 1;\n  padding: 3px 4px;\n}\n","// --------------------------------------------------------------\n// Captions\n// --------------------------------------------------------------\n\n// Hide default captions\n.plyr--full-ui ::-webkit-media-text-track-container {\n  display: none;\n}\n\n.plyr__captions {\n  animation: plyr-fade-in 0.3s ease;\n  bottom: 0;\n  display: none;\n  font-size: $plyr-font-size-captions-small;\n  left: 0;\n  padding: $plyr-control-spacing;\n  position: absolute;\n  text-align: center;\n  transition: transform 0.4s ease-in-out;\n  width: 100%;\n\n  span:empty {\n    display: none;\n  }\n\n  @media (min-width: $plyr-bp-sm) {\n    font-size: $plyr-font-size-captions-base;\n    padding: calc(#{$plyr-control-spacing} * 2);\n  }\n\n  @media (min-width: $plyr-bp-md) {\n    font-size: $plyr-font-size-captions-medium;\n  }\n}\n\n.plyr--captions-active .plyr__captions {\n  display: block;\n}\n\n// If the lower controls are shown and not empty\n.plyr:not(.plyr--hide-controls) .plyr__controls:not(:empty) ~ .plyr__captions {\n  transform: translateY(calc(#{$plyr-control-spacing} * -4));\n}\n\n.plyr__caption {\n  background: $plyr-captions-background;\n  border-radius: 2px;\n  box-decoration-break: clone;\n  color: $plyr-captions-text-color;\n  line-height: 185%;\n  padding: 0.2em 0.5em;\n  white-space: pre-wrap;\n\n  // Firefox adds a <div> when using getCueAsHTML()\n  div {\n    display: inline;\n  }\n}\n","// --------------------------------------------------------------\n// Control buttons\n// --------------------------------------------------------------\n\n.plyr__control {\n  background: transparent;\n  border: 0;\n  border-radius: $plyr-control-radius;\n  color: inherit;\n  cursor: pointer;\n  flex-shrink: 0;\n  overflow: visible; // IE11\n  padding: $plyr-control-padding;\n  position: relative;\n  transition: all 0.3s ease;\n\n  svg {\n    display: block;\n    fill: currentColor;\n    height: $plyr-control-icon-size;\n    pointer-events: none;\n    width: $plyr-control-icon-size;\n  }\n\n  // Default focus\n  &:focus {\n    outline: 0;\n  }\n\n  // Tab focus\n  &.plyr__tab-focus {\n    @include plyr-tab-focus();\n  }\n}\n\n// Remove any link styling\na.plyr__control {\n  text-decoration: none;\n\n  &::after,\n  &::before {\n    display: none;\n  }\n}\n\n// Change icons on state change\n.plyr__control:not(.plyr__control--pressed) .icon--pressed,\n.plyr__control.plyr__control--pressed .icon--not-pressed,\n.plyr__control:not(.plyr__control--pressed) .label--pressed,\n.plyr__control.plyr__control--pressed .label--not-pressed {\n  display: none;\n}\n","// --------------------------------------------------------------\n// Controls\n// --------------------------------------------------------------\n\n// Hide native controls\n.plyr--full-ui ::-webkit-media-controls {\n  display: none;\n}\n\n// Playback controls\n.plyr__controls {\n  align-items: center;\n  display: flex;\n  justify-content: flex-end;\n  text-align: center;\n\n  .plyr__progress__container {\n    flex: 1;\n    min-width: 0; // Fix for Edge issue where content would overflow\n  }\n\n  // Spacing\n  .plyr__controls__item {\n    margin-left: calc(#{$plyr-control-spacing} / 4);\n\n    &:first-child {\n      margin-left: 0;\n      margin-right: auto;\n    }\n\n    &.plyr__progress__container {\n      padding-left: calc(#{$plyr-control-spacing} / 4);\n    }\n\n    &.plyr__time {\n      padding: 0 calc(#{$plyr-control-spacing} / 2);\n    }\n\n    &.plyr__progress__container:first-child,\n    &.plyr__time:first-child,\n    &.plyr__time + .plyr__time {\n      padding-left: 0;\n    }\n  }\n\n  // Hide empty controls\n  &:empty {\n    display: none;\n  }\n}\n\n// Some options are hidden by default\n.plyr [data-plyr='captions'],\n.plyr [data-plyr='pip'],\n.plyr [data-plyr='airplay'],\n.plyr [data-plyr='fullscreen'] {\n  display: none;\n}\n.plyr--captions-enabled [data-plyr='captions'],\n.plyr--pip-supported [data-plyr='pip'],\n.plyr--airplay-supported [data-plyr='airplay'],\n.plyr--fullscreen-enabled [data-plyr='fullscreen'] {\n  display: inline-block;\n}\n","// --------------------------------------------------------------\n// Menus\n// --------------------------------------------------------------\n\n.plyr__menu {\n  display: flex; // Edge fix\n  position: relative;\n\n  // Animate the icon\n  .plyr__control svg {\n    transition: transform 0.3s ease;\n  }\n  .plyr__control[aria-expanded='true'] {\n    svg {\n      transform: rotate(90deg);\n    }\n\n    // Hide tooltip\n    .plyr__tooltip {\n      display: none;\n    }\n  }\n\n  // The actual menu container\n  &__container {\n    animation: plyr-popup 0.2s ease;\n    background: $plyr-menu-background;\n    border-radius: 4px;\n    bottom: 100%;\n    box-shadow: $plyr-menu-shadow;\n    color: $plyr-menu-color;\n    font-size: $plyr-font-size-base;\n    margin-bottom: 10px;\n    position: absolute;\n    right: -3px;\n    text-align: left;\n    white-space: nowrap;\n    z-index: 3;\n\n    > div {\n      overflow: hidden;\n      transition: height 0.35s cubic-bezier(0.4, 0, 0.2, 1), width 0.35s cubic-bezier(0.4, 0, 0.2, 1);\n    }\n\n    // Arrow\n    &::after {\n      border: $plyr-menu-arrow-size solid transparent;\n      border-top-color: $plyr-menu-background;\n      content: '';\n      height: 0;\n      position: absolute;\n      right: calc(((#{$plyr-control-icon-size} / 2) + #{$plyr-control-padding}) - (#{$plyr-menu-arrow-size} / 2));\n      top: 100%;\n      width: 0;\n    }\n\n    [role='menu'] {\n      padding: $plyr-control-padding;\n    }\n\n    [role='menuitem'],\n    [role='menuitemradio'] {\n      margin-top: 2px;\n\n      &:first-child {\n        margin-top: 0;\n      }\n    }\n\n    // Options\n    .plyr__control {\n      align-items: center;\n      color: $plyr-menu-color;\n      display: flex;\n      font-size: $plyr-font-size-menu;\n      padding-bottom: calc(#{$plyr-control-padding} / 1.5);\n      padding-left: calc(#{$plyr-control-padding} * 1.5);\n      padding-right: calc(#{$plyr-control-padding} * 1.5);\n      padding-top: calc(#{$plyr-control-padding} / 1.5);\n      user-select: none;\n      width: 100%;\n\n      > span {\n        align-items: inherit;\n        display: flex;\n        width: 100%;\n      }\n\n      &::after {\n        border: $plyr-menu-item-arrow-size solid transparent;\n        content: '';\n        position: absolute;\n        top: 50%;\n        transform: translateY(-50%);\n      }\n\n      &--forward {\n        padding-right: calc(#{$plyr-control-padding} * 4);\n\n        &::after {\n          border-left-color: $plyr-menu-item-arrow-color;\n          right: calc((#{$plyr-control-padding} * 1.5) - #{$plyr-menu-item-arrow-size});\n        }\n\n        &.plyr__tab-focus::after,\n        &:hover::after {\n          border-left-color: currentColor;\n        }\n      }\n\n      &--back {\n        font-weight: $plyr-font-weight-regular;\n        margin: $plyr-control-padding;\n        margin-bottom: calc(#{$plyr-control-padding} / 2);\n        padding-left: calc(#{$plyr-control-padding} * 4);\n        position: relative;\n        width: calc(100% - (#{$plyr-control-padding} * 2));\n\n        &::after {\n          border-right-color: $plyr-menu-item-arrow-color;\n          left: calc((#{$plyr-control-padding} * 1.5) - #{$plyr-menu-item-arrow-size});\n        }\n\n        &::before {\n          background: $plyr-menu-back-border-color;\n          box-shadow: 0 1px 0 $plyr-menu-back-border-shadow-color;\n          content: '';\n          height: 1px;\n          left: 0;\n          margin-top: calc(#{$plyr-control-padding} / 2);\n          overflow: hidden;\n          position: absolute;\n          right: 0;\n          top: 100%;\n        }\n\n        &.plyr__tab-focus::after,\n        &:hover::after {\n          border-right-color: currentColor;\n        }\n      }\n    }\n\n    .plyr__control[role='menuitemradio'] {\n      padding-left: $plyr-control-padding;\n\n      &::before,\n      &::after {\n        border-radius: 100%;\n      }\n\n      &::before {\n        background: rgba(#000, 0.1);\n        content: '';\n        display: block;\n        flex-shrink: 0;\n        height: 16px;\n        margin-right: $plyr-control-spacing;\n        transition: all 0.3s ease;\n        width: 16px;\n      }\n\n      &::after {\n        background: #fff;\n        border: 0;\n        height: 6px;\n        left: 12px;\n        opacity: 0;\n        top: 50%;\n        transform: translateY(-50%) scale(0);\n        transition: transform 0.3s ease, opacity 0.3s ease;\n        width: 6px;\n      }\n\n      &[aria-checked='true'] {\n        &::before {\n          background: $plyr-control-toggle-checked-background;\n        }\n        &::after {\n          opacity: 1;\n          transform: translateY(-50%) scale(1);\n        }\n      }\n\n      &.plyr__tab-focus::before,\n      &:hover::before {\n        background: rgba($plyr-color-gray-900, 0.1);\n      }\n    }\n\n    // Option value\n    .plyr__menu__value {\n      align-items: center;\n      display: flex;\n      margin-left: auto;\n      margin-right: calc((#{$plyr-control-padding} - 2) * -1);\n      overflow: hidden;\n      padding-left: calc(#{$plyr-control-padding} * 3.5);\n      pointer-events: none;\n    }\n  }\n}\n","// --------------------------------------------------------------\n// Slider inputs - <input type=\"range\">\n// --------------------------------------------------------------\n\n.plyr--full-ui input[type='range'] {\n  -webkit-appearance: none; /* stylelint-disable-line */\n  background: transparent;\n  border: 0;\n  border-radius: calc(#{$plyr-range-thumb-height} * 2);\n  // `color` property is used in JS to populate lower fill for WebKit\n  color: $plyr-range-fill-background;\n  display: block;\n  height: calc((#{$plyr-range-thumb-active-shadow-width} * 2) + #{$plyr-range-thumb-height});\n  margin: 0;\n  min-width: 0;\n  padding: 0;\n  transition: box-shadow 0.3s ease;\n  width: 100%;\n\n  &::-webkit-slider-runnable-track {\n    @include plyr-range-track();\n    background-image: linear-gradient(to right, currentColor var(--value, 0%), transparent var(--value, 0%));\n  }\n\n  &::-webkit-slider-thumb {\n    @include plyr-range-thumb();\n    -webkit-appearance: none; /* stylelint-disable-line */\n    margin-top: calc(((#{$plyr-range-thumb-height} - #{$plyr-range-track-height}) / 2) * -1);\n  }\n\n  // Mozilla\n  &::-moz-range-track {\n    @include plyr-range-track();\n  }\n\n  &::-moz-range-thumb {\n    @include plyr-range-thumb();\n  }\n\n  &::-moz-range-progress {\n    background: currentColor;\n    border-radius: calc(#{$plyr-range-track-height} / 2);\n    height: $plyr-range-track-height;\n  }\n\n  // Microsoft\n  &::-ms-track {\n    @include plyr-range-track();\n    color: transparent;\n  }\n\n  &::-ms-fill-upper {\n    @include plyr-range-track();\n  }\n\n  &::-ms-fill-lower {\n    @include plyr-range-track();\n\n    background: currentColor;\n  }\n\n  &::-ms-thumb {\n    @include plyr-range-thumb();\n    // For some reason, Edge uses the -webkit margin above\n    margin-top: 0;\n  }\n\n  &::-ms-tooltip {\n    display: none;\n  }\n\n  // Focus styles\n  &:focus {\n    outline: 0;\n  }\n\n  &::-moz-focus-outer {\n    border: 0;\n  }\n\n  &.plyr__tab-focus {\n    &::-webkit-slider-runnable-track {\n      @include plyr-tab-focus();\n    }\n\n    &::-moz-range-track {\n      @include plyr-tab-focus();\n    }\n\n    &::-ms-track {\n      @include plyr-tab-focus();\n    }\n  }\n}\n","// --------------------------------------------------------------\n// Faux poster overlay\n// --------------------------------------------------------------\n\n.plyr__poster {\n  background-color: var(--plyr-video-background, $plyr-video-background);\n  background-position: 50% 50%;\n  background-repeat: no-repeat;\n  background-size: contain;\n  height: 100%;\n  left: 0;\n  opacity: 0;\n  position: absolute;\n  top: 0;\n  transition: opacity 0.2s ease;\n  width: 100%;\n  z-index: 1;\n}\n\n.plyr--stopped.plyr__poster-enabled .plyr__poster {\n  opacity: 1;\n}\n","// --------------------------------------------------------------\n// Time\n// --------------------------------------------------------------\n\n.plyr__time {\n  font-size: $plyr-font-size-time;\n}\n\n// Media duration hidden on small screens\n.plyr__time + .plyr__time {\n  // Add a slash in before\n  &::before {\n    content: '\\2044';\n    margin-right: $plyr-control-spacing;\n  }\n\n  @media (max-width: ($plyr-bp-md - 1px)) {\n    display: none;\n  }\n}\n","// --------------------------------------------------------------\n// Tooltips\n// --------------------------------------------------------------\n\n.plyr__tooltip {\n  background: $plyr-tooltip-background;\n  border-radius: $plyr-tooltip-radius;\n  bottom: 100%;\n  box-shadow: $plyr-tooltip-shadow;\n  color: $plyr-tooltip-color;\n  font-size: $plyr-font-size-small;\n  font-weight: $plyr-font-weight-regular;\n  left: 50%;\n  line-height: 1.3;\n  margin-bottom: calc(#{$plyr-tooltip-padding} * 2);\n  opacity: 0;\n  padding: $plyr-tooltip-padding calc(#{$plyr-tooltip-padding} * 1.5);\n  pointer-events: none;\n  position: absolute;\n  transform: translate(-50%, 10px) scale(0.8);\n  transform-origin: 50% 100%;\n  transition: transform 0.2s 0.1s ease, opacity 0.2s 0.1s ease;\n  white-space: nowrap;\n  z-index: 2;\n\n  // The background triangle\n  &::before {\n    border-left: $plyr-tooltip-arrow-size solid transparent;\n    border-right: $plyr-tooltip-arrow-size solid transparent;\n    border-top: $plyr-tooltip-arrow-size solid $plyr-tooltip-background;\n    bottom: calc(#{$plyr-tooltip-arrow-size} * -1);\n    content: '';\n    height: 0;\n    left: 50%;\n    position: absolute;\n    transform: translateX(-50%);\n    width: 0;\n    z-index: 2;\n  }\n}\n\n// Displaying\n.plyr .plyr__control:hover .plyr__tooltip,\n.plyr .plyr__control.plyr__tab-focus .plyr__tooltip,\n.plyr__tooltip--visible {\n  opacity: 1;\n  transform: translate(-50%, 0) scale(1);\n}\n\n.plyr .plyr__control:hover .plyr__tooltip {\n  z-index: 3;\n}\n\n// First tooltip\n.plyr__controls > .plyr__control:first-child .plyr__tooltip,\n.plyr__controls > .plyr__control:first-child + .plyr__control .plyr__tooltip {\n  left: 0;\n  transform: translate(0, 10px) scale(0.8);\n  transform-origin: 0 100%;\n\n  &::before {\n    left: calc((#{$plyr-control-icon-size} / 2) + #{$plyr-control-padding});\n  }\n}\n\n// Last tooltip\n.plyr__controls > .plyr__control:last-child .plyr__tooltip {\n  left: auto;\n  right: 0;\n  transform: translate(0, 10px) scale(0.8);\n  transform-origin: 100% 100%;\n\n  &::before {\n    left: auto;\n    right: calc((#{$plyr-control-icon-size} / 2) + #{$plyr-control-padding});\n    transform: translateX(50%);\n  }\n}\n\n.plyr__controls > .plyr__control:first-child,\n.plyr__controls > .plyr__control:first-child + .plyr__control,\n.plyr__controls > .plyr__control:last-child {\n  &:hover .plyr__tooltip,\n  &.plyr__tab-focus .plyr__tooltip,\n  .plyr__tooltip--visible {\n    transform: translate(0, 0) scale(1);\n  }\n}\n","// --------------------------------------------------------------\n// Playback progress\n// --------------------------------------------------------------\n\n// Offset the range thumb in order to be able to calculate the relative progress (#954)\n$plyr-progress-offset: $plyr-range-thumb-height;\n\n.plyr__progress {\n  left: calc(#{$plyr-progress-offset} * 0.5);\n  margin-right: $plyr-progress-offset;\n  position: relative;\n\n  input[type='range'],\n  &__buffer {\n    margin-left: calc(#{$plyr-progress-offset} * -0.5);\n    margin-right: calc(#{$plyr-progress-offset} * -0.5);\n    width: calc(100% + #{$plyr-progress-offset});\n  }\n\n  input[type='range'] {\n    position: relative;\n    z-index: 2;\n  }\n\n  // Seek tooltip to show time\n  .plyr__tooltip {\n    font-size: $plyr-font-size-time;\n    left: 0;\n  }\n}\n\n.plyr__progress__buffer {\n  -webkit-appearance: none; /* stylelint-disable-line */\n  background: transparent;\n  border: 0;\n  border-radius: 100px;\n  height: $plyr-range-track-height;\n  left: 0;\n  margin-top: calc((#{$plyr-range-track-height} / 2) * -1);\n  padding: 0;\n  position: absolute;\n  top: 50%;\n\n  &::-webkit-progress-bar {\n    background: transparent;\n  }\n\n  &::-webkit-progress-value {\n    background: currentColor;\n    border-radius: 100px;\n    min-width: $plyr-range-track-height;\n    transition: width 0.2s ease;\n  }\n\n  // Mozilla\n  &::-moz-progress-bar {\n    background: currentColor;\n    border-radius: 100px;\n    min-width: $plyr-range-track-height;\n    transition: width 0.2s ease;\n  }\n\n  // Microsoft\n  &::-ms-fill {\n    border-radius: 100px;\n    transition: width 0.2s ease;\n  }\n}\n\n// Loading state\n.plyr--loading .plyr__progress__buffer {\n  animation: plyr-progress 1s linear infinite;\n  background-image: linear-gradient(\n    -45deg,\n    $plyr-progress-loading-background 25%,\n    transparent 25%,\n    transparent 50%,\n    $plyr-progress-loading-background 50%,\n    $plyr-progress-loading-background 75%,\n    transparent 75%,\n    transparent\n  );\n  background-repeat: repeat-x;\n  background-size: $plyr-progress-loading-size $plyr-progress-loading-size;\n  color: transparent;\n}\n\n.plyr--video.plyr--loading .plyr__progress__buffer {\n  background-color: $plyr-video-progress-buffered-background;\n}\n\n.plyr--audio.plyr--loading .plyr__progress__buffer {\n  background-color: $plyr-audio-progress-buffered-background;\n}\n","// --------------------------------------------------------------\n// Volume\n// --------------------------------------------------------------\n\n.plyr__volume {\n  align-items: center;\n  display: flex;\n  max-width: 110px;\n  min-width: 80px;\n  position: relative;\n  width: 20%;\n\n  input[type='range'] {\n    margin-left: calc(#{$plyr-control-spacing} / 2);\n    margin-right: calc(#{$plyr-control-spacing} / 2);\n    position: relative;\n    z-index: 2;\n  }\n}\n\n// Auto size on iOS as there's no slider\n.plyr--is-ios .plyr__volume {\n  min-width: 0;\n  width: auto;\n}\n","// --------------------------------------------------------------\n// Audio styles\n// --------------------------------------------------------------\n\n// Container\n.plyr--audio {\n  display: block;\n}\n\n// Controls container\n.plyr--audio .plyr__controls {\n  background: $plyr-audio-controls-background;\n  border-radius: inherit;\n  color: $plyr-audio-control-color;\n  padding: $plyr-control-spacing;\n}\n\n// Control elements\n.plyr--audio .plyr__control {\n  &.plyr__tab-focus,\n  &:hover,\n  &[aria-expanded='true'] {\n    background: $plyr-audio-control-background-hover;\n    color: $plyr-audio-control-color-hover;\n  }\n}\n\n// Range inputs\n.plyr--full-ui.plyr--audio input[type='range'] {\n  &::-webkit-slider-runnable-track {\n    background-color: $plyr-audio-range-track-background;\n  }\n\n  &::-moz-range-track {\n    background-color: $plyr-audio-range-track-background;\n  }\n\n  &::-ms-track {\n    background-color: $plyr-audio-range-track-background;\n  }\n\n  // Pressed styles\n  &:active {\n    &::-webkit-slider-thumb {\n      @include plyr-range-thumb-active($plyr-audio-range-thumb-active-shadow-color);\n    }\n\n    &::-moz-range-thumb {\n      @include plyr-range-thumb-active($plyr-audio-range-thumb-active-shadow-color);\n    }\n\n    &::-ms-thumb {\n      @include plyr-range-thumb-active($plyr-audio-range-thumb-active-shadow-color);\n    }\n  }\n}\n\n// Progress\n.plyr--audio .plyr__progress__buffer {\n  color: $plyr-audio-progress-buffered-background;\n}\n","// --------------------------------------------------------------\n// Video styles\n// --------------------------------------------------------------\n\n// Container\n.plyr--video {\n  background: var(--plyr-video-background, $plyr-video-background);\n  overflow: hidden;\n\n  &.plyr--menu-open {\n    overflow: visible;\n  }\n}\n\n.plyr__video-wrapper {\n  background: var(--plyr-video-background, $plyr-video-background);\n  height: 100%;\n  margin: auto;\n  overflow: hidden;\n  position: relative;\n  width: 100%;\n}\n\n// Default to 16:9 ratio but this is set by JavaScript based on config\n$embed-padding: ((100 / 16) * 9);\n\n.plyr__video-embed,\n.plyr__video-wrapper--fixed-ratio {\n  height: 0;\n  padding-bottom: to-percentage($embed-padding);\n}\n\n.plyr__video-embed iframe,\n.plyr__video-wrapper--fixed-ratio video {\n  border: 0;\n  left: 0;\n  position: absolute;\n  top: 0;\n}\n\n// If the full custom UI is supported\n.plyr--full-ui .plyr__video-embed {\n  $height: 240;\n  $offset: to-percentage(($height - $embed-padding) / ($height / 50));\n\n  // Only used for Vimeo\n  > .plyr__video-embed__container {\n    padding-bottom: to-percentage($height);\n    position: relative;\n    transform: translateY(-$offset);\n  }\n}\n\n// Controls container\n.plyr--video .plyr__controls {\n  background: $plyr-video-controls-background;\n  border-bottom-left-radius: inherit;\n  border-bottom-right-radius: inherit;\n  bottom: 0;\n  color: $plyr-video-control-color;\n  left: 0;\n  padding: calc(#{$plyr-control-spacing} / 2);\n  padding-top: calc(#{$plyr-control-spacing} * 2);\n  position: absolute;\n  right: 0;\n  transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;\n  z-index: 3;\n\n  @media (min-width: $plyr-bp-sm) {\n    padding: $plyr-control-spacing;\n    padding-top: calc(#{$plyr-control-spacing} * 3.5);\n  }\n}\n\n// Hide controls\n.plyr--video.plyr--hide-controls .plyr__controls {\n  opacity: 0;\n  pointer-events: none;\n  transform: translateY(100%);\n}\n\n// Control elements\n.plyr--video .plyr__control {\n  // Hover and tab focus\n  &.plyr__tab-focus,\n  &:hover,\n  &[aria-expanded='true'] {\n    background: $plyr-video-control-background-hover;\n    color: $plyr-video-control-color-hover;\n  }\n}\n\n// Large play button (video only)\n.plyr__control--overlaid {\n  background: $plyr-video-control-background-hover;\n  border: 0;\n  border-radius: 100%;\n  color: $plyr-video-control-color;\n  display: none;\n  left: 50%;\n  opacity: 0.9;\n  padding: calc(#{$plyr-control-spacing} * 1.5);\n  position: absolute;\n  top: 50%;\n  transform: translate(-50%, -50%);\n  transition: 0.3s;\n  z-index: 2;\n\n  // Offset icon to make the play button look right\n  svg {\n    left: 2px;\n    position: relative;\n  }\n\n  &:hover,\n  &:focus {\n    opacity: 1;\n  }\n}\n\n.plyr--playing .plyr__control--overlaid {\n  opacity: 0;\n  visibility: hidden;\n}\n\n.plyr--full-ui.plyr--video .plyr__control--overlaid {\n  display: block;\n}\n\n// Video range inputs\n.plyr--full-ui.plyr--video input[type='range'] {\n  &::-webkit-slider-runnable-track {\n    background-color: $plyr-video-range-track-background;\n  }\n\n  &::-moz-range-track {\n    background-color: $plyr-video-range-track-background;\n  }\n\n  &::-ms-track {\n    background-color: $plyr-video-range-track-background;\n  }\n\n  // Pressed styles\n  &:active {\n    &::-webkit-slider-thumb {\n      @include plyr-range-thumb-active($plyr-video-range-thumb-active-shadow-color);\n    }\n\n    &::-moz-range-thumb {\n      @include plyr-range-thumb-active($plyr-video-range-thumb-active-shadow-color);\n    }\n\n    &::-ms-thumb {\n      @include plyr-range-thumb-active($plyr-video-range-thumb-active-shadow-color);\n    }\n  }\n}\n\n// Progress\n.plyr--video .plyr__progress__buffer {\n  color: $plyr-video-progress-buffered-background;\n}\n","// --------------------------------------------------------------\n// Fullscreen\n// --------------------------------------------------------------\n\n.plyr:fullscreen {\n  @include plyr-fullscreen-active();\n}\n\n/* stylelint-disable-next-line */\n.plyr:-webkit-full-screen {\n  @include plyr-fullscreen-active();\n}\n\n/* stylelint-disable-next-line */\n.plyr:-moz-full-screen {\n  @include plyr-fullscreen-active();\n}\n\n/* stylelint-disable-next-line */\n.plyr:-ms-fullscreen {\n  @include plyr-fullscreen-active();\n}\n\n// Fallback for unsupported browsers\n.plyr--fullscreen-fallback {\n  @include plyr-fullscreen-active();\n  bottom: 0;\n  display: block;\n  left: 0;\n  position: fixed;\n  right: 0;\n  top: 0;\n  z-index: 10000000;\n}\n","// ==========================================================================\n// Advertisements\n// ==========================================================================\n\n.plyr__ads {\n  border-radius: inherit;\n  bottom: 0;\n  cursor: pointer;\n  left: 0;\n  overflow: hidden;\n  position: absolute;\n  right: 0;\n  top: 0;\n  z-index: -1; // Hide it by default\n\n  // Make sure the inner container is big enough for the ad creative.\n  > div,\n  > div iframe {\n    height: 100%;\n    position: absolute;\n    width: 100%;\n  }\n\n  // The countdown label\n  &::after {\n    background: $plyr-color-gray-900;\n    border-radius: 2px;\n    bottom: $plyr-control-spacing;\n    color: #fff;\n    content: attr(data-badge-text);\n    font-size: 11px;\n    padding: 2px 6px;\n    pointer-events: none;\n    position: absolute;\n    right: $plyr-control-spacing;\n    z-index: 3;\n  }\n\n  &::after:empty {\n    display: none;\n  }\n}\n\n// Advertisement cue's for the progress bar\n.plyr__cues {\n  background: currentColor;\n  display: block;\n  height: $plyr-range-track-height;\n  left: 0;\n  margin: -($plyr-range-track-height / 2) 0 0;\n  opacity: 0.8;\n  position: absolute;\n  top: 50%;\n  width: 3px;\n  z-index: 3; // Between progress and thumb\n}\n","// --------------------------------------------------------------\n// Preview Thumbnails\n// --------------------------------------------------------------\n\n@import './settings';\n\n.plyr__preview-thumb {\n  background-color: $plyr-preview-background;\n  border-radius: 3px;\n  bottom: 100%;\n  box-shadow: $plyr-preview-shadow;\n  margin-bottom: calc(#{$plyr-preview-padding} * 2);\n  opacity: 0;\n  padding: $plyr-preview-radius;\n  pointer-events: none;\n  position: absolute;\n  transform: translate(0, 10px) scale(0.8);\n  transform-origin: 50% 100%;\n  transition: transform 0.2s 0.1s ease, opacity 0.2s 0.1s ease;\n  z-index: 2;\n\n  &--is-shown {\n    opacity: 1;\n    transform: translate(0, 0) scale(1);\n  }\n\n  // The background triangle\n  &::before {\n    border-left: $plyr-preview-arrow-size solid transparent;\n    border-right: $plyr-preview-arrow-size solid transparent;\n    border-top: $plyr-preview-arrow-size solid $plyr-preview-background;\n    bottom: calc(#{$plyr-preview-arrow-size} * -1);\n    content: '';\n    height: 0;\n    left: 50%;\n    position: absolute;\n    transform: translateX(-50%);\n    width: 0;\n    z-index: 2;\n  }\n\n  &__image-container {\n    background: $plyr-preview-image-background;\n    border-radius: calc(#{$plyr-preview-radius} - 1px);\n    overflow: hidden;\n    position: relative;\n    z-index: 0;\n\n    img {\n      height: 100%; // Non sprite images are 100%. Sprites will have their size applied by JavaScript\n      left: 0;\n      max-height: none;\n      max-width: none;\n      position: absolute;\n      top: 0;\n      width: 100%;\n    }\n  }\n\n  // Seek time text\n  &__time-container {\n    bottom: $plyr-preview-time-bottom-offset;\n    left: 0;\n    position: absolute;\n    right: 0;\n    white-space: nowrap;\n    z-index: 3;\n\n    span {\n      background-color: $plyr-preview-time-background;\n      border-radius: calc(#{$plyr-preview-radius} - 1px);\n      color: $plyr-preview-time-color;\n      font-size: $plyr-preview-time-font-size;\n      padding: $plyr-preview-time-padding;\n    }\n  }\n}\n\n.plyr__preview-scrubbing {\n  bottom: 0;\n  filter: blur(1px);\n  height: 100%;\n  left: 0;\n  margin: auto; // Required when video is different dimensions to container (e.g. fullscreen)\n  opacity: 0;\n  overflow: hidden;\n  pointer-events: none;\n  position: absolute;\n  right: 0;\n  top: 0;\n  transition: opacity 0.3s ease;\n  width: 100%;\n  z-index: 1;\n\n  &--is-shown {\n    opacity: 1;\n  }\n\n  img {\n    height: 100%;\n    left: 0;\n    max-height: none;\n    max-width: none;\n    object-fit: contain;\n    position: absolute;\n    top: 0;\n    width: 100%;\n  }\n}\n","// --------------------------------------------------------------\n// Animation utils\n// --------------------------------------------------------------\n\n.plyr--no-transition {\n  transition: none !important;\n}\n","// --------------------------------------------------------------\n// Hiding content nicely\n// --------------------------------------------------------------\n\n// Screen reader only elements\n.plyr__sr-only {\n  clip: rect(1px, 1px, 1px, 1px);\n  overflow: hidden;\n\n  // !important is not always needed\n  @if $plyr-sr-only-important {\n    border: 0 !important;\n    height: 1px !important;\n    padding: 0 !important;\n    position: absolute !important;\n    width: 1px !important;\n  } @else {\n    border: 0;\n    height: 1px;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n  }\n}\n\n.plyr [hidden] {\n  display: none !important;\n}\n","html {\n  height: 100%;\n  scroll-behavior: smooth;\n}\n\nbody {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  background-color: #fff;\n  font-family: $font--body;\n\n  &.home {\n    // background:\n    //   url(\"../images/bg-triangle-alt.svg\") 0 100%,\n    //   url(\"../images/bg-triangle-alt.svg\") 91px 100%, $brand--primary;\n  }\n\n  &.front-page-data {\n    .page-header {\n      display: none;\n    }\n  }\n\n  &.archive {\n    background-color: $brand--white;\n  }\n}\n\np {\n  color: $brand--primary;\n  font-size: 14px;\n  letter-spacing: 0.5px;\n  text-align: left;\n  line-height: 18px;\n  margin-top: 0;\n}\n\n.inner {\n  @include inner;\n}\n\n.about,\n.home,\n.front-page-data {\n  .wrap {\n    padding: 0 0 0 0;\n  }\n}\n\n.wrap {\n  flex: 1 0 auto;\n  padding: 120px 0 0 0;\n  margin: 0 auto;\n  max-width: 100%;\n  width: 100%;\n\n  @include breakpoint(medium) {\n    padding: 136px 0 0 0;\n  }\n}\n\n.single-data {\n  .wrap {\n    padding: 130px 0 0 0;\n\n    @include breakpoint(medium) {\n      padding: 139px 0 0 0;\n    }\n  }\n}\n\nimg {\n  width: 100%;\n  height: auto;\n}\n\n.ico-search {\n  display: block;\n  background: url(\"../images/ico-search.svg\");\n  background-size: 16px;\n  width: 16px;\n  height: 16px;\n}\n\n.ico-heart {\n  display: block;\n  background: url(\"../images/ico-heart.svg\");\n  background-size: 24px;\n  width: 24px;\n  height: 22px;\n}\n\n.ico-account {\n  display: block;\n  background: url(\"../images/ico-account.svg\");\n  background-size: 16px;\n  width: 16px;\n  height: 15px;\n}\n\n.ico-basket {\n  display: block;\n  background: url(\"../images/ico-basket.svg\");\n  background-size: 20px;\n  width: 20px;\n  height: 15px;\n}\n\n.bg-color {\n  &__blue {\n    background-color: #bfdbe0;\n  }\n}\n\n.alt {\n  background-color: #dde2ea;\n}\n\n.alert {\n  padding: 60px 0;\n\n  &__inner {\n    @include inner;\n  }\n}\n\n.about-page {\n  .section {\n    &:nth-of-type(2n) {\n      padding-top: 0;\n    }\n  }\n}\n\n.woocommerce-error {\n  list-style: none;\n  margin: 0;\n  padding: 0 0 20px 0;\n\n  a {\n    @include subtitle($brand--primary);\n  }\n}\n\n.message {\n  &__inner {\n    @include inner;\n\n    padding-bottom: 300px;\n  }\n}\n","@mixin breakpoint($point) {\n  @if $point == xx-large {\n    @media (min-width: 2200px) { @content; }\n  }\n  @if $point == x-large {\n    @media (min-width: 1700px) { @content; }\n  }\n  @if $point == large {\n     @media (min-width: 1200px) { @content; }\n  }\n  @else if $point == medium {\n     @media (min-width: 900px) { @content; }\n  }\n  @else if $point == small {\n     @media (min-width: 600px) { @content; }\n  }\n}\n\n@mixin inner {\n  padding: 0 20px;\n  max-width: $content-width;\n  margin: 0 auto;\n  @include breakpoint(medium) {\n    padding: 0 50px;\n  }\n}\n\n@mixin section {\n  padding: 35px 0;\n\n  @include breakpoint(medium) {\n    padding: 35px 0;\n  }\n}\n\n@mixin hoverline($color) {\n  display: inline-block;\n  position: relative;\n  color: $color;\n  line-height: 15px;\n  text-decoration: none;\n\n  // &::after {\n  //   content: \"\";\n  //   position: absolute;\n  //   width: 100%;\n  //   transform: scaleX(0);\n  //   height: 1px;\n  //   bottom: -3px;\n  //   left: 0;\n  //   background-color: $color;\n  //   transform-origin: bottom;\n  //   transition: transform 0.4s cubic-bezier(0.86, 0,